.thead-dark{

  background-color: rgb(235, 172, 135);
  padding: 20px;
  

}

.container-fluid{
  width: 90%;
  margin: auto;
}
.table-responsive{
  max-height: 50px;
  box-shadow: rgba(0,0,0,0.25) 0px 25px 50px -12px;

}
tr:hover{
  background-color: rgb(219, 219, 29);
  color: rgb(241, 241, 245);
}
.center{
  padding: 30px;
  margin-right: auto;
  margin-left: auto;
  background-color: rgb(247, 122, 19);

  
}
td,
th{
  position: relative;
  padding: 20px;
  background-color: rgb(163, 150, 142);
}
.table td,
.table th{

  padding: 1.5rem;
  vertical-align: top;
  border-top: none;
  
}

td:hover::after,
th:hover::after{

  content:"";
  position:absolute;
  background-color: silver;
  /* #f2f2f2 */
  left: 0;
  /* top: -50px;
  height: 50px; */
  width: 100%;
  z-index: -1;

}

.table tr td:hover:nth-child(3){

  background-color: rgb(231,83,115);
  color: aliceblue;
  border: 0.5px solid rgb(12,12,12);
}

.table tr td:hover{

  background-color: rgb(117,223,122);
  color: aliceblue;
  border: 0.5px solid rgb(12,12,12);
}

.thead-dark{
  font-weight: bold;
  text-transform: uppercase;
}

